body {
  background-color: lightgrey;
}

.Headertable {
  margin: 0px auto;
  width: 100%;
}

.Headercolm {
  width: 33%;
}

.Sidenav {
  height: 100%;
  width: 0px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  padding-top: 60px;
  white-space: nowrap;
}

.Sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 23px;
  color: #818181;
  display: block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.Sidenav a:hover {
  color: white;
  background-color: black;
}

.Opennav, .Closenav {
  color: white;
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;
  outline:none;
}

.Closenav {
  position: absolute;
  top: 0;
  right: 20px;
  margin-left: 50px;
  font-size: 40px;
}

.Opennav {
  float: left;
  padding-left: 10px;
  padding-bottom: 15px;
  font-size: 30px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 0px;
  color: white;
}

.App-footer, .App-footer p{
  opacity: 0;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.App-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #222;
  color: white;
  text-align: center;
  display: inline-block;
  height: 0px;
  
  
}

.App-footer p {
  margin: 0px;
  padding: 0px;
  padding-bottom: 10px;
}

.Divider {
  padding-left: 50px;
  display: inline-block;
}

.Footerbutton {
  background-color: white;
  color: black;
  border: none;
  padding: 5px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 13px;
}

.Footerbutton:hover {
  background-color: #222;
  color: white;
}

.App-title {
  font-size: 1em;
}

.App-intro {
  font-size: medium;
}

.Boxed {
  background-color: white;
  /*width: 50%*/
  width: 460px;
  border: 0px;
  padding: 15px;
  margin: 20px auto;

}

.Expandable {
  
  overflow: hidden;
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  -webkit-transform-origin: left top;
      -ms-transform-origin: left top;
          transform-origin: left top;
  -webkit-transform: scaleY(0);
      -ms-transform: scaleY(0);
          transform: scaleY(0);
}

.Expandable:target {
  -webkit-transform: scaleY(1);
      -ms-transform: scaleY(1);
          transform: scaleY(1);
}

a { 
  text-decoration: none; 
  color: black;
}

@media screen and (max-height: 450px) {
  .Sidenav {padding-top: 15px;}
  .Sidenav a {font-size: 18px;}
  }


